.overview {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.overviewContent {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 0;
}

.topBar {
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 16px;
  padding: 16px 0;
  background: var(--gray-50);
  border-bottom: 1px solid var(--gray-200);
  margin-bottom: 24px;
}

.timeframeSelect {
  display: flex;
  background: var(--gray-100);
  border-radius: var(--radius-md);
  padding: 4px;
  gap: 4px;
  width: fit-content;
}

.timeframeBtn {
  padding: 8px 16px;
  border: none;
  background: transparent;
  border-radius: var(--radius-sm);
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-600);
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}

.timeframeBtn.active {
  background: var(--white);
  color: var(--midnight);
  box-shadow: var(--shadow-sm);
}

.refreshBtn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: 1px solid var(--gray-200);
  background: var(--white);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-700);
  cursor: pointer;
  transition: all 0.2s;
}

.refreshBtn:hover:not(:disabled) {
  border-color: var(--azure);
  color: var(--azure);
}

.refreshBtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spinning {
  animation: spin 1s linear infinite;
}

